import { Col, Container, Row } from 'react-bootstrap';

import './all-inclusive-body.css';

const highlights = [
  'Bufet de desayuno americano completo y estaciones de cocina en vivo',
  'Desayunos continentales / meriendas',
  'Buffet frío y caliente y estaciones de cocina en vivo',
  'Pizzas, pastas, ensaladas y postres variados',
  'Buffets y platos preparados en directo',
  'Minibar y dispensador de licores reabastecidos periódicamente',
  'Bebidas nacionales e importadas servidas las 24 horas',
  'Gimnasio',
  'RiuFit, actividades de fitness en grupo (diario)',
  'Bañera de hidromasaje',
  'Cancha de voleibol de playa',
  'Equipos de catamarán, piragüismo y snorkel',
  '1 lección introductoria de buceo en piscina',
  'Programa de entretenimiento diurno para adultos ((varias veces por semana)',
  'Música en vivo, espectáculos o programa nocturno Riu (diario)',
];

function AllInclusiveBody() {
  return (
    <div className="restaurants-bar-body-div pt-5 pb-5">
      <Container>
        <Row className="justify-content-md-center text-center mb-5">
          <Col lg="8">
            <h1>Detalles Todo Incluido</h1>
          </Col>
        </Row>
        <Row className="justify-content-md-center text-left mb-5">
          <Col lg="10">
            <ul className="default-list">
              {highlights.map((value, i) => {
                return (
                  <li key={i}>
                    <i className="fa fa-check-circle"></i> {value}
                  </li>
                );
              })}
            </ul>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default AllInclusiveBody;
