import { useEffect } from 'react';
import AllInclusiveBody from '../component/all-inclusive-body/all-inclusive-body';
import Hero from '../component/hero/hero';

function AllInclusivePage() {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <div>
      <Hero
        images={[
          {
            url: '/images/hotel/all-inclusive-1.jpg',
          },
          {
            url: '/images/hotel/all-inclusive-2.jpg',
          },
          {
            url: '/images/hotel/all-inclusive-3.jpg',
          },
        ]}
      />
      <AllInclusiveBody />
    </div>
  );
}

export default AllInclusivePage;
